import React from 'react'
import { Link } from 'gatsby'

import { colors } from '../theme'

import { matterNameFromMatter, matterDescriptionFromMatter } from '../helpers'

import { Card, CardBody, CardHeader, CardTitle } from 'reactstrap'

export const MatterPreview = ({ node }) => {
  const matterName = matterNameFromMatter(node.matter)
  const description = matterDescriptionFromMatter(node.matter)
  return (
    <Card className="card-matter-preview" color={colors.matters[node.matter]}>
      <CardHeader>
        <CardTitle>
          <Link to={node.path}>
            <strong className="display-5">{matterName}</strong>
          </Link>
        </CardTitle>
      </CardHeader>
      <CardBody>
        <blockquote>{description}</blockquote>
        <a href={`/${node.classe}/${node.matter}/programme`}>
          <em className="cite-source">Programme officiel</em>
        </a>
      </CardBody>
    </Card>
  )
}
